import {updateLevelByCode} from '@/api/mine.js'
  export default {
    name: 'mineMyVip',
    data() {
      return {
        code: ''
      }
    },
    methods: {
      getUpdateLevelByCode() {
        if (this.code == ''){
          return this.$vux.toast.show({
            text: '请输入邀请码',
            type: 'text'
          })
        }
        let prams = {
          code: this.code,
          useId: this.$store.getters.userInfo.id
        }
        updateLevelByCode(prams).then(res => {
          this.$vux.toast.show({
            text: res.msg,
            type: 'text'
          })
        })
      }
    }
  }



